@import url(https://fonts.googleapis.com/css?family=Heebo:400,500,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .home{
	width:100%;
	height: 100%;
	background-color: #282c34;
}

.App {

	width:100%;
	height: 100%;
}

.league-button {
	width: 230px;
	margin: 80px 15px 0px;
}

.league-button.league-button-selected {
	color: green;
	border-color: green;
}
.team-select {
	display: block;
	width: 400px;

	margin: 40px auto auto;
}

.home {
	display: flex;
}

@media only screen and (max-width: 700px) {
	.home {
		display: block;
	}
}
.power-rankings-container {
	max-width: 420px;


	color: white;
	margin-top:15px;
	/*background-color: white;*/
}
.power-rankings-body {
	padding: 25px 20px 10px 10px;
	font-family: Heebo, 'Alef', sans-serif;
	max-height: 550px;
	overflow-y: auto;
}

.team-title {
	position: relative;
	margin-bottom: 10px;
}
.team-title .name, .record {
	text-align: center;
}
.team-title .rank {
	font-size: 35px;
	position: absolute;
	right:0;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	direction: rtl;
}
.team-info {
	border: 1px solid rgb(220, 221, 223);
	padding: 10px;
	font-size: 13px;
}
.prevrank-cell {
	font-size: 15px;
}
table.team-info-table {
	width: 100%;
	border-collapse:separate;
}
.arrow-img {
	margin-left:2px;
}
.team-analysis {
	margin-top:8px;
	direction: rtl;
	margin-bottom:25px;
}
.analysis-author {
	font-style: italic;
}
.arrow-img {
	margin-left: 4px;
	position: relative;
	top: -1px;
}

.team > hr {
	background-color: #a5a6a7;
	border: 0;
	color: #a5a6a7;
	height: 1px;
	margin-bottom: 26px;
}
.power-rankings-logo {
	height:  130px;
	margin: auto;
	display: block;
}
@media only screen and (max-width: 850px) {
	.power-rankings-logo {
		height:  90px;
	}
}
.author-title {
	font-size: 18px;
	font-weight: 500;
}
